<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckArticle :id="thisId" :checkAnyway="true"/>
            <AuditForm v-model="auditForm" @submit="handleSubmit"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../新闻列表/components/TopButtons'
    import CheckArticle from '@/components/article/check/Index'
    import {auditArticle} from '@/api/article'
    import AuditForm from '@/components/checkSuffixForm/audit'

    export default {
        components: {
            TopButtons, CheckArticle, AuditForm
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                comment: {},
                auditForm: {
                    is_passed: null,
                    reason: null,
                },
            }
        },
        methods: {
            handleSubmit() {
                let data = {
                    status: this.auditForm.is_passed ? 1 : 2,
                    reason: this.auditForm.reason
                };
                auditArticle(this.id, data)
                    .then(res => {
                        this.$message.success('操作成功');
                    })
            },
        },
    }
</script>
